.card {
    display: grid;
    grid-template-columns: 2fr 10fr;
    grid-gap: 15px;
    padding: 17px;

    margin: 5px 20px 5px 20px;
    background-color: #202830;
    color: aqua;
}

.leftSideData {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-right: 1px solid orange;
}

.rightSideDescr {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.descr {
    display: flex;
    flex-grow: .5;
    flex-direction: column;
    justify-content: space-between;
}

.photo {
    width: 100px;
    height: 100px;
    margin: 10px auto;
    border-radius: 50%;
    border: 2px solid orange;
}

.govno {
    border: 0;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
}

.button {
    padding: 15px;
    width: 100px;
    margin: 0 auto;
    border-radius: 7px;
    background-color: rgba(0,0,0,.0);
    box-shadow: none;
    border: 2px solid orange;
    color: aqua;
}

.status {
    color: orange;
    text-align: right;
    font-weight: bolder;
    border-bottom: 1px solid orange;
}